import { Component, OnInit, ViewEncapsulation, ViewChild, ChangeDetectionStrategy, ChangeDetectorRef } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { Subject } from 'rxjs';

import { MatDrawerToggleResult } from '@angular/material/sidenav';

import { EnquiryListComponent } from 'src/app/modules/admin/enquiries/enquiry-list/enquiry-list.component';

import { SharedService } from 'src/app/shared/services/shared.service';

@Component({
  selector: 'app-enquiry-comments',
  templateUrl: './enquiry-comments.component.html',
  styleUrls: ['./enquiry-comments.component.scss']
})
export class EnquiryCommentsComponent implements OnInit {

  isDataLoaded:boolean = true;

  private _unsubscribeAll: Subject<any>;

  constructor(
    private _activatedRoute: ActivatedRoute,
    private _router: Router,
    private _changeDetectorRef: ChangeDetectorRef,
    private _enquiryListComponent: EnquiryListComponent,
    private sharedService:SharedService
  ) {

     // Set the private defaults
     this._unsubscribeAll = new Subject();

  }

  closeDrawer(): Promise<MatDrawerToggleResult>{
    return this._enquiryListComponent.matDrawer.close();
  }

  goBack() {
    this._enquiryListComponent.matDrawer.close();
    this._router.navigate(['.'], {relativeTo: this._activatedRoute.parent});
  }

  ngOnInit(): void {

    //Open the drawer
    this.sharedService.matdraweridcast.subscribe(id => {

      if(id != null && id == this._activatedRoute.params['value'].id){
        this._enquiryListComponent.matDrawer.open();
      }

      //Mark for check
      this._changeDetectorRef.markForCheck();

    })

  }

}
